<template>
  <div>
    <div v-if="step==0">
        <postCompo :data="data[idx]" v-for="(d,idx) in data" :key="idx"></postCompo>
    </div>
    <!-- Filter select Page -->
    <div v-if="step==1">
        <div :class="filterName + ' filter-item'" class="upload-image" :style="`background-image:url(${image});`">
            
        </div>
        <div class="filters">
            <filterboxCompo  :image="image" :filter="filter" v-for="filter in filters" :key="filter">
                <span>{{ filter }}</span>
            </filterboxCompo>
        </div>
    </div>

    <!-- Post writing Page -->
    <div v-if="step==2">
        <div :class="filterName" class="upload-image" :style="`background-image:url(${image});`"></div>
        <div class="write">
            <textarea @input="$emit('write',$event.target.value)" class="write-box">write!</textarea>
        </div>
    </div>

  </div>
</template>

<script>
import postCompo from './postCompo.vue'
import filterboxCompo from './filterCompe.vue';

export default {
    name:'containerCompo',
    components:{
        postCompo,
        filterboxCompo
    },
    props:{
        data:Object,
        step:Number,
        image:String,
    },
    data(){
        return{
            filters:[ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
            "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
            "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
            filterName:''        
        }
    },
    mounted(){
        this.emitter.on('boxClicked',(filtername)=>{
            this.filterName = filtername;
        });
    },
}
</script>

